import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

 import { ProjectHistoryPage } from '../project-history/project-history';
 import { TaskListPage } from '../task-list/task-list';
import { ProjectNearPage } from '../project-near/project-near';

@Component({
  selector: 'project-detail',
  templateUrl: 'project-detail.html',
  styleUrls:['/pages/project-detail/porject-detail.scss']
})
export class ProjectDetailPage {

  constructor(public navtrl:NavController){

  }
  //客户历史项目清单 
  projectHistory(){
    this.navtrl.push(ProjectHistoryPage);
  }
  //任务清单
  taskList(){
    this.navtrl.push(TaskListPage);
  }
  //查看附件项目
  projectNear(){
    this.navtrl.push(ProjectNearPage);
  }
  taskNear(){
    this.navtrl.push('TaskNearPage');
  }
}





