import { Component, OnInit, ViewChild } from '@angular/core';
import { NavController,NavParams, ViewController  } from 'ionic-angular';

declare var L: any;

@Component({
  selector: 'page-pointAdd-modal',
  templateUrl: 'point-add.html'
})
export class PointAddModalPage implements OnInit {
  private map: any;
  private selectData:any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController) {
  }
  ngOnInit() {
    let VectorBasemap = L.esri.basemapLayer('GoogleVex');
    let SatelliteBasemap = L.esri.basemapLayer('GoogleSat');
    let VectorLayer = L.layerGroup([VectorBasemap]);
    let SatelliteLayer = L.layerGroup([SatelliteBasemap]);
    let BaseLayers = { "地图": VectorLayer, "影像": SatelliteLayer};
    let curZoom = this.navParams.get('zoom');
    let curCenter = this.navParams.get('center');

    this.map = new L.map('mapModal', {
      center: curCenter,
      zoom: curZoom,
      crs: L.CRS.EPSG3857,
      attributionControl: false,
      zoomControl: false,
      layers: [VectorLayer]
    });
    L.control.layers(BaseLayers, null).addTo(this.map);
  }
  dismiss() {
    this.viewCtrl.dismiss();
  }
  submit(){
    let mapCurZoom = this.map.getZoom();
    let mapCurCenter = this.map.getCenter();
    this.viewCtrl.dismiss({
      center: mapCurCenter,
      zoom: mapCurZoom
    });
  }

}



