import {  ModalFromBottomLeave, ModalFromBottomEnter ,ModalSlideIn,ModalSlideOut,ModalMDSlideIn,ModalMDSlideOut} from './modal-transition';
import { Config } from 'ionic-angular';

import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

//plugins
import { Camera } from '@ionic-native/camera';

import { MyApp } from './app.component';
import { TabsPage } from './../pages/tabs/tabs';
import { HomePage } from '../pages/home/home';
import { ContactPage } from '../pages/contact/contact';
import { AccountPage } from '../pages/account/account';

import { LoginPage } from '../pages/login/login';

import { ProjectListPage } from '../pages/project-list/project-list';
import { ProjectDetailPage } from '../pages/project-detail/project-detail'; 
import { ProjectHistoryPage } from '../pages/project-history/project-history';
import { ProjectHistoryDetailPage } from '../pages/project-historyDetail/project-historyDetail';
import { TaskListPage } from './../pages/task-list/task-list';
import { TaskDetailPage } from '../pages/task-detail/task-detail';
import { TaskAddPage } from '../pages/task-add/task-add';
import { ProjectNearPage } from '../pages/project-near/project-near';
import { RoutePage } from '../pages/route/route';
import { MapAddressPage} from '../pages/map-address/map-address';
import { PointAddModalPage } from '../pages/route/point-add-modal/point-add';
import { TransferboxModalPage } from '../pages/route/transforbox-modal/transferbox-modal';
import { TransferboxAddModalPage } from '../pages/route/transferbox-add-modal/transferbox-add-modal';
//import { MySlide } from '../components/my-side';
import { RouteDesignPage } from '../pages/route-design/route-design';
import { RouteDesignModalPage } from '../pages/route-design/route-design-modal/route-design-add';
import { RouteLinePage } from '../pages/route-line/route-line';
import { LedupModalPage } from '../pages/route-design/ledup-modal/ledup-modal';

import { TransferBoxPage } from '../pages/transferbox/transferbox';
import { CoreBitPage } from '../pages/core-bit/core-bit';
import { ResourceAddPage } from '../pages/resource-add/resource-add';
import { PicResourcesPage } from '../pages/pic-resources/pic-resources';
import { SunveyNotesPage } from '../pages/sunvey-notes/sunvey-notes';

import { AdvisoryPage } from '../pages/advisory/advisory';
//import { ComponentModule } from '../components/component.module';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ContactPage,
    AccountPage,
    TabsPage,
    LoginPage,
    ProjectListPage,
    ProjectDetailPage,
    ProjectHistoryPage,
    ProjectHistoryDetailPage,
    TaskListPage,
    TaskDetailPage,
    TaskAddPage,
    ProjectNearPage,
    RoutePage,
    MapAddressPage,
    PointAddModalPage,
    TransferboxModalPage,
    TransferboxAddModalPage,
    RouteLinePage,
    RouteDesignPage,
    RouteDesignModalPage,
    LedupModalPage,
    TransferBoxPage,
    CoreBitPage,
    ResourceAddPage,
    PicResourcesPage,
    SunveyNotesPage,
    AdvisoryPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      tabsHideOnSubPages: 'true',         //隐藏全部子页面tabs
      //backButtonText: '',  
      //iconMode: 'ios',  
      //mode: 'ios',
    }, 
{
  links: [
    { loadChildren: '../pages/route/point-search/point-search.module#PointSearchPageModule', name: 'PointSearchPage', segment: 'point-search', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/task-near/task-near.module#TaskNearPageModule', name: 'TaskNearPage', segment: 'task-near', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/task-near-list/task-near-list.module#TaskNearListPageModule', name: 'TaskNearListPage', segment: 'task-near-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/task-near-resources/task-near-resources.module#TaskNearResourcesPageModule', name: 'TaskNearResourcesPage', segment: 'task-near-resources', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ContactPage,
    AccountPage,
    TabsPage,
    LoginPage,
    ProjectListPage,
    ProjectDetailPage,
    ProjectHistoryPage,
    ProjectHistoryDetailPage,
    TaskListPage,
    TaskDetailPage,
    TaskAddPage,
    ProjectNearPage,
    RoutePage,
    MapAddressPage,
    PointAddModalPage,
    TransferboxModalPage,
    TransferboxAddModalPage,
    RouteLinePage,
    RouteDesignPage,
    RouteDesignModalPage,
    LedupModalPage,
    TransferBoxPage,
    CoreBitPage,
    ResourceAddPage,
    PicResourcesPage,
    SunveyNotesPage,
    AdvisoryPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Camera,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {
  constructor(public config: Config) {
    this.setCustomTransitions();
  }

  private setCustomTransitions() {
    this.config.setTransition('modal-from-bottom-enter', ModalFromBottomEnter);
    this.config.setTransition('modal-from-bottom-leave', ModalFromBottomLeave);
    // this.config.setTransition('modal-enter-1', ModalSlideIn);
    // this.config.setTransition('modal-leave-1', ModalSlideOut);
    // this.config.setTransition('modal-enter', ModalMDSlideIn);
    // this.config.setTransition('modal-leave', ModalMDSlideOut);
  }
}



