import { RouteDesignModalPage } from './route-design-modal/route-design-add';
import { Component, OnInit } from '@angular/core';
import { NavController,ModalController } from 'ionic-angular';
//import { MySlide } from "../../components/my-side";

import { LedupModalPage } from './ledup-modal/ledup-modal';

declare var L:any;

@Component({
  selector: 'route-design',
  templateUrl: 'route-design.html',
  styleUrls:['/pages/route-design/route-design.scss']
})
export class RouteDesignPage implements OnInit{
  flag:boolean = true;
  resourceFlag: boolean = false;
  showTransferboxFlag: boolean = false;
  showPipelineFlag:boolean = false;
  showBarsFlag:boolean = false;
  showWallFlag:boolean = false;
  map:any;
  featureLayer:any;
  layersToolBar:null;
  drawToolBar:null;

  constructor(public navCtrl: NavController, public modalCtrl: ModalController) {

  }

   ngOnInit() {
    let curPosition = [23.129529, 113.380216];
    let VectorBasemap = L.esri.basemapLayer('GoogleVex');
    let VectorLayer = L.layerGroup([VectorBasemap]);
    let SatBasemap = L.esri.basemapLayer('GoogleSat');
    let SatLayer = L.layerGroup([SatBasemap]);

    this.map = L.map('routeDesign', {
      center: curPosition,
      zoom: 14,
      crs: L.CRS.EPSG3857,
      attributionControl: true,
      zoomControl: false,
    });

    let drawnItems = L.featureGroup().bindPopup('我是画图要素').addTo(this.map);
    this.layersToolBar = L.control.layers({
        '谷歌地图': VectorLayer.addTo(this.map),
        "谷歌影像": SatLayer
    }, { '绘图图层': drawnItems }, { position: 'topleft', collapsed: false });

    L.drawLocal.draw.toolbar.actions.text = '取消';
    L.drawLocal.draw.toolbar.finish.text = '完成';
    L.drawLocal.draw.toolbar.undo.text = '撤销';
    L.drawLocal.draw.toolbar.buttons.polyline = '绘画路线';
    L.drawLocal.draw.handlers.polyline.tooltip.start = null;
    L.drawLocal.draw.handlers.polyline.tooltip.cont = null;
    L.drawLocal.draw.handlers.polyline.tooltip.end = null;
    L.drawLocal.draw.handlers.marker.tooltip.start = null;
    L.drawLocal.edit.handlers.edit.tooltip.subtext = null;
    L.drawLocal.edit.handlers.edit.tooltip.text = null;
    L.drawLocal.edit.handlers.remove.tooltip.text = null;

    this.drawToolBar = new L.Control.Draw({
      edit: {
        featureGroup: drawnItems,
        poly: {
          allowIntersection: false
        }
      },
      draw: {
        // polygon: {
        //   allowIntersection: false,
        //   showArea: true
        // },
        // polyline: {
        //   shapeOptions: {
        //       color: '#f357a1',
        //       weight: 10
        //   },
        // },
        polygon: false,
        circle: false,
        rectangle:false,
        circlemarker: false,
        // marker: {
        //   icon: MyCustomMarker
        // }
      }
    });
    //添加图层
    // this.map.addControl(layersToolBar);
    // this.map.addControl(drawToolBar);

    // this.map.removeControl(layersToolBar);
    // this.map.removeControl(drawToolBar);

    this.map.on(L.Draw.Event.CREATED, (event)=>{
      var type = event.layerType,
      layer = event.layer;
      if (type === 'polyline') {
          //TODO 画完线的工作
          this.showPipeline();
      }
      drawnItems.addLayer(layer);
    });
    this.featureLayer = L.featureGroup()
    .bindPopup((layer)=> {
      //return layer.options.content.title + ":"+ layer.options.content.address;
      return "<p style='font-weight:bolder'>交接箱</p><p>名称：" + layer.options.content.title+ "</p><p class='transferboxTip'>位置：" +layer.options.content.address+"</p><p class='transferboxTip'>芯位：" + "48</p>";
    })
    // .on('click', function() { return; })
    .addTo(this.map);
  }

  // ngOnInit(){
  //   var map = new L.map('routeDesign',{
  //     crs:L.CRS.EPSG3857,
  //     attributionControl: false,
  //     zoomControl: false
  //   }).setView([23.129529,113.380216], 14);
  //   let basemap = L.esri.basemapLayer('GaodeVec');
  //   basemap.addTo(map);
  //   this.slides = [];
  //   for(let i=0; i < 8; i++){
  //     this.slides.push({
  //       title:'万科项目二' + i,
  //       image:'assets/img/wanke.jpg',
  //       name:'刘伟',
  //       distance:'1'+ i + '公里'
  //     })
  //   }
  // }
  //路由设计
  routeButton(){
    if(!!this.flag){
      this.flag = false;
      this.map.addControl(this.layersToolBar);
      this.map.addControl(this.drawToolBar);
    }else{
      this.flag = true;
          //添加图层

    }
  }

   //显示资源信息
  showResource() {
    if (!!!this.resourceFlag) {
      this.resourceFlag = true;
    } else {
      this.resourceFlag = false;
    }
  }
  //添加交接箱
  showTransferbox() {
    let mapCurZoom = this.map.getZoom();
    let mapCurCenter = this.map.getCenter();
    let modal = this.modalCtrl.create(RouteDesignModalPage, {
      zoom: mapCurZoom, 
      center: mapCurCenter
    });
    let mytransferbox = L.icon({
        iconUrl: 'assets/lib/images/transferbox-icon-2x.png',
        iconSize: [25, 41],
        iconAnchor: [12.5, 41],
        // popupAnchor: [-3, -76],
        //shadowUrl: 'assets/lib/images/marker-shadow.png',
        //shadowSize: [25, 41],
        //shadowAnchor: [20.5, 20.5]
    });
    modal.onDidDismiss(data => {
      if(!!!data) return;
      
     let marker = L.marker(data.center,{icon:mytransferbox, content: data.content});
     let markerContent = data.content;
     this.featureLayer.addLayer(marker);
     this.map.fitBounds(this.featureLayer.getBounds());

    if (!!!this.showTransferboxFlag) {
      this.showTransferboxFlag = true;
    } else {
      this.showTransferboxFlag = false;
    }
   });
    modal.present();

  }

  closeTransferbox(){
    if (!!!this.showTransferboxFlag) {
      this.showTransferboxFlag = true;
    } else {
      this.showTransferboxFlag = false;
    }
  }

  //添加光缆管道
  showPipeline() {
    if (!!!this.showPipelineFlag) {
      this.showPipelineFlag = true;
    } else {
      this.showPipelineFlag = false;
    }
  }

  closePipeline(){
    if (!!!this.showPipelineFlag) {
      this.showPipelineFlag = true;
    } else {
      this.showPipelineFlag = false;
    }
  }

  //添加杠路吊线
  showBars(){
    if (!!!this.showBarsFlag) {
      this.showBarsFlag = true;
    } else {
      this.showBarsFlag = false;
    }
  }

  closeBars(){
    if (!!!this.showBarsFlag) {
      this.showBarsFlag = true;
    } else {
      this.showBarsFlag = false;
    }
  }

  //添加墙壁吊线
  showWall(){
    if (!!!this.showWallFlag) {
      this.showWallFlag = true;
    } else {
      this.showWallFlag = false;
    }
  }

  closeWall(){
    if (!!!this.showWallFlag) {
      this.showWallFlag = true;
    } else {
      this.showWallFlag = false;
    }
  }

  addLedupPoint(){
    let mapCurZoom = this.map.getZoom();
    let mapCurCenter = this.map.getCenter();
    let modal = this.modalCtrl.create(LedupModalPage, {
      zoom: mapCurZoom, 
      center: mapCurCenter
    });
    modal.onDidDismiss(data => {
      if(!!!data) return;
     let marker = L.marker(data.center);
     this.featureLayer.addLayer(marker);
     this.map.fitBounds(this.featureLayer.getBounds());
   });
    modal.present();
  }

}



