
export class LatLng {
    public latitude: number;
    public longitude: number;

    constructor(latitude, longitude) {
        this.latitude = latitude;
        this.longitude = longitude;
    }
}
export class mapUtils {
    /**
     * 地图定位功能
     * 
     * @memberof HomePage
     */
    public static locate(): Promise<any> {
        if(!navigator.geolocation){
            alert("地图地位不可用!");
        }
        return new Promise((resolve, reject) => {
            let latitude: number = 0, longitude: number = 0;

            const GeolocationOptions = {
                enableHighAccuracy: true, ///精细定位
                timeout: 5000, ///定位超时时间
                maximumAge: 3000 /// 最大请求时间
            };
            navigator.geolocation.getCurrentPosition(position => {
                resolve(new LatLng(position.coords.latitude, position.coords.longitude));
            }, error => {
                switch (error.code) {
                    case error.PERMISSION_DENIED:
                        console.log("User denied the request for Geolocation.");
                        break;
                    case error.POSITION_UNAVAILABLE:
                        console.log("Location information is unavailable.");
                        break;
                    case error.TIMEOUT:
                        console.log("The request to get user location timed out.");
                        console.log("Unable to start geolocation. Check application settings.");
                        break;
                }
                reject(error.code);
            }, GeolocationOptions);
        });
    }
  
}


