import { Component } from '@angular/core';
import { NavController, AlertController, Platform } from 'ionic-angular';

import { TaskDetailPage } from '../task-detail/task-detail'; 
import { TaskAddPage } from '../task-add/task-add';

@Component({
  selector: 'task-list',
  templateUrl: 'task-list.html'
})
export class TaskListPage {
  pet: string = "unfinished";
 // placeholder:string = "请输入项目名称、客户单位、地点";
  isAndroid: boolean = false;
  testRadioOpen:boolean;
  testRadioResult;
  constructor(public navCtrl: NavController,platform: Platform, public alertCtrl:AlertController) {
    this.isAndroid = platform.is('android');
  }

  taskDetail(){
    this.navCtrl.push(TaskDetailPage);
  }

  taskAdd(){
    this.navCtrl.push(TaskAddPage);
  }

  slectPerson(){
    let alert = this.alertCtrl.create();
    alert.setTitle('选择人员');

    alert.addInput({
      type:'radio',
      label:'刘健',
      value:'刘健',
      checked:true
    });

    alert.addInput({
      type:'radio',
      label:'刘健2',
      value:'刘健2'
    });

    alert.addButton('取消');
    alert.addButton({
      text:'确定',
      handler:data =>{
        this.testRadioOpen = false;
        this.testRadioResult = data;
      }
    });

    alert.present().then(() => {
      this.testRadioOpen = true;
    })

  }

}



