import { Component, OnInit, ViewChild } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import {CoordinateUtil} from "../../../libriary/CoordinateUtil";
import {LatLng} from '../../../libriary/mapUtils';


declare var L: any;

@Component({
  selector: 'transferbox-add-modal',
  templateUrl: 'transferbox-add-modal.html'
})
export class TransferboxAddModalPage implements OnInit {
  private map: any;
  private selectData: any;
  private title: string;//点的标题
  private address: string = "";// 当前坐标的地名地址
  private addressArray: Array<any> = new Array<any>();//存储地名地址服务的数组
  private addressIndex:number = -1;//当前地名地址服务数组的索引
  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController) {
  }
  ngOnInit() {
    let VectorBasemap = L.esri.basemapLayer('GoogleVex');
    let SatelliteBasemap = L.esri.basemapLayer('GoogleSat');
    let VectorLayer = L.layerGroup([VectorBasemap]);
    let SatelliteLayer = L.layerGroup([SatelliteBasemap]);
    let BaseLayers = { "地图": VectorLayer, "影像": SatelliteLayer };
    let curZoom = this.navParams.get('zoom');
    let curCenter = this.navParams.get('center');

    this.map = new L.map('transferboxAddModal', {
      center: curCenter,
      zoom: curZoom,
      crs: L.CRS.EPSG3857,
      attributionControl: false,
      zoomControl: false,
      layers: [VectorLayer]
    });
    //绑定地图移动结束事件
    this.map.on("moveend", this.moveEvent);
    L.control.layers(BaseLayers, null).addTo(this.map);
  }
  dismiss() {
    this.viewCtrl.dismiss();
  }
  submit() {
    let mapCurZoom = this.map.getZoom();
    let mapCurCenter = this.map.getCenter();
    this.viewCtrl.dismiss({
      center: mapCurCenter,
      zoom: mapCurZoom,
      content: {
        title: this.title,
        address: this.address
      }
    });
  }
  private getJson(path: string) {
    // 在IE7下测试通过，IE6下必须创建 new ActiveXObject("MSXML2.XMLHTTP.6.0")
    return new Promise<any>((resolve, reject) => {
      let request = new XMLHttpRequest();

      request.open('GET', path);
      request.onreadystatechange = () => {
        if (request.readyState == 4 && request.status == 200) {
          let objs = JSON.parse(request.responseText);
          resolve(objs);
        }
      }
      request.onerror = (err) => {
        reject(err);
      }
      request.send(null);
    });

  }
  adjust(){
    
    if(this.addressIndex < 0 && this.addressArray.length === 0){
      return;
    }
    this.addressIndex ++;
    if(this.addressArray.length <= this.addressIndex){
      this.addressIndex = 0;
    }
    let tmpAddress = this.addressArray[this.addressIndex];

    this.address = tmpAddress.Address + "---"+ tmpAddress.PlaceName;
    // this.map.off("moveend");
    // this.map.setView([tmpAddress.Lat, tmpAddress.Lon]);
    // this.map.on("moveend", this.moveEvent);
  }
  private moveEvent= (e)=> {
    this.address = ".......";
    let center = this.map.getCenter();
    let bdpoint: LatLng = CoordinateUtil.transformFromGCJToBD(new LatLng(center.lat, center.lng));
    let geturl = "http://172.16.50.133:18400/Baidu/Position?lon=" + bdpoint.longitude + "&lat=" + bdpoint.latitude;
    this.getJson(geturl).then(
      data => {
        this.addressArray = data;
        this.adjust();
      },
      err => {

      }
    );
  }
}



