import { Component, OnInit } from '@angular/core';
import { NavController,ModalController } from 'ionic-angular';
import { mapUtils, LatLng } from '../../libriary/mapUtils';
import {GeoJsonUtil} from'../../libriary/GeoJsonUtil';
import {MapAddressPage } from '../map-address/map-address';

//import { MySlide } from "../../components/my-side";

declare var L: any;

@Component({
  selector: 'project-near',
  templateUrl: 'project-near.html',
  styleUrls: ['/pages/project-near/project-near.scss']
})
export class ProjectNearPage implements OnInit {

  // pageSlides: string[] = ["测试1", "测试2", "测试3", "测试4", "测试5", "测试6", "测试7", "测试8"];
  pageNumber: number = 2;
  slides: Array<{ title: string, image: any, name: string, distance: string }>;
  private map: any;
  private islarge:boolean = false;
  private svgxml:string = 'assets/lib/images/method-draw-image.svg';
  constructor(public navCtrl: NavController, public modalCtrl: ModalController) {
  //   let svg = '<svg width="580" height="400" xmlns="http://www.w3.org/2000/svg">'+
  //   '<g>' +
  //     '<title>background</title>' +
  //     '<rect fill="#fff" id="canvas_background" height="402" width="582" y="-1" x="-1"/>' +
  //    '<g display="none" overflow="visible" y="0" x="0" height="100%" width="100%" id="canvasGrid">' +
  //     '<rect fill="url(#gridpattern)" stroke-width="0" y="0" x="0" height="100%" width="100%"/>' +
  //    '</g>' + 
  //   '</g>' +
  //   '<g>' +
  //    '<title>Layer 1</title>' + 
  //    '<ellipse ry="28.5" rx="31.5" id="svg_1" cy="148.5" cx="266" stroke-width="1.5" stroke="#000" fill="#CE7975"/>' +
  //   '</g>' +
  //  '</svg>';
  //   this.svgxml = 'data:image/svg+xml;base64,' + window.btoa(decodeURIComponent(encodeURIComponent(svg))); //给图片对象写入base64编码的svg流    
  }
  ngOnInit() {
    let curPosition = [23.129529, 113.380216];
    let VectorBasemap = L.esri.basemapLayer('GoogleVex');
    let VectorLayer = L.layerGroup([VectorBasemap]);

    this.map = L.map('map', {
      center: curPosition,
      zoom: 14,
      crs: L.CRS.EPSG3857,
      attributionControl: false,
      layers: [VectorLayer]
      //zoomControl: false,
    });
    //地图定位
    mapUtils.locate().then(
      (latlng: LatLng) => {
        curPosition = [latlng.latitude, latlng.longitude];
        this.map.setView(curPosition, 14);
      }).catch(
      (err) => {
        //TODO somthing wrong
      });

    //加载数据
    this.getJson("assets/json/geojson.json").then(
      data => {
        let geoJSON: any = data;
        this.slides = geoJSON.features;
        GeoJsonUtil.tramsformCoordinate(geoJSON.features);
        let layer = L.geoJSON(geoJSON).addTo(this.map, {
          style: function (feature) {
            return {};
          },
          pointToLayer: function (geoJsonPoint, latlng) {
            return L.marker(latlng);
          }
        }).bindPopup((layer)=> {
          // let myIcon = L.icon({
          //   iconUrl: 'assets/lib/images/mymarker.png',
          //   iconSize: [32, 32],
          //   iconAnchor: [16, 32],
          // });
          // layer.setIcon(myIcon);
          return "编号值:" + layer.feature.properties.code;

        });
        this.map.fitBounds(layer.getBounds());
        let tempP = [ 23.124008117101754,113.37358843143204];
        
        let myIcon = L.icon({
          iconUrl: 'assets/lib/images/mymarker.png',
          iconSize: [48, 48],
          iconAnchor: [24, 48],
          shadowUrl: 'assets/lib/images/marker-shadow.png',
          shadowSize: [68, 95],
          shadowAnchor: [22, 94]
        });
        L.marker(tempP, {icon: myIcon}).addTo(this.map);
      }).catch(
      err => {
        console.log(err);
      });

    // this.slides = [];
    // for (let i = 0; i < 8; i++) {
    //   this.slides.push({
    //     title: '万科项目二' + i,
    //     image: 'assets/img/wanke.jpg',
    //     name: '刘伟',
    //     distance: '1' + i + '公里'
    //   })
    // }
  }
  private getJson(path: string) {
    // 在IE7下测试通过，IE6下必须创建 new ActiveXObject("MSXML2.XMLHTTP.6.0")
    return new Promise<any>((resolve, reject) => {
      let request = new XMLHttpRequest();

      request.open('GET', path);
      request.onreadystatechange = () => {
        if (request.readyState == 4 && request.status == 200) {
          let objs = JSON.parse(request.responseText);
          resolve(objs);
        }
      }
      request.onerror = (err) => {
        reject(err);
      }
      request.send(null);
    });

  }
  private addMap(){
    let modal = this.modalCtrl.create(MapAddressPage);
    modal.onDidDismiss(data => {
     console.log(data);
   });
    modal.present();
  }
  private enlarge(){
    this.islarge = !this.islarge;
  }

}



