import { Component, OnInit } from '@angular/core';
import { NavController,ModalController } from 'ionic-angular';
//import { MySlide } from "../../components/my-side";
import { RouteDesignPage } from '../route-design/route-design';
import { TransferBoxPage } from '../transferbox/transferbox';
import { PointAddModalPage } from './point-add-modal/point-add';
import { TransferboxModalPage } from './transforbox-modal/transferbox-modal';
import { TransferboxAddModalPage } from './transferbox-add-modal/transferbox-add-modal';

declare var L: any;

@Component({
  selector: 'route',
  templateUrl: 'route.html',
  styleUrls: ['/pages/route/route.scss']
})
export class RoutePage implements OnInit {

  flag: boolean = false;
  layerFlag: boolean = false;
  private map: any;
  private demandPoints: Array<any> = new Array<any>();
  private featureLayer:any = null; //画图要素的图层

  constructor(public navCtrl: NavController, public modalCtrl: ModalController) {

  }
  ngOnInit() {
    let curPosition = [23.129529, 113.380216];
    let VectorBasemap = L.esri.basemapLayer('GoogleVex');
    let VectorLayer = L.layerGroup([VectorBasemap]);
    let SatBasemap = L.esri.basemapLayer('GoogleSat');
    let SatLayer = L.layerGroup([SatBasemap]);

    this.map = L.map('map', {
      center: curPosition,
      zoom: 14,
      crs: L.CRS.EPSG3857,
      attributionControl: true,
      zoomControl: false,
    });
    VectorLayer.addTo(this.map);

    // let drawnItems = L.featureGroup().bindPopup('Hello world!').addTo(this.map);

    // L.control.layers({
    //     '谷歌地图': VectorLayer.addTo(this.map),
    //     "谷歌影像": SatLayer
    // }, { 'drawlayer': drawnItems }, { position: 'topleft', collapsed: false }).addTo(this.map);
    // this.map.addControl(new L.Control.Draw({
    //   edit: {
    //     featureGroup: drawnItems,
    //     poly: {
    //       allowIntersection: false
    //     }
    //   },
    //   draw: {
    //     polygon: {
    //       allowIntersection: false,
    //       showArea: true
    //     }
    //   }
    // }));

    // this.map.on(L.Draw.Event.CREATED, (event)=>{
    //   let layer = event.layer;
    //   drawnItems.addLayer(layer);
    // });

    this.featureLayer = L.featureGroup()
    .bindPopup(`<div><button onclick="showInfo()">详情</button></div>`)
    .on('click', function() { alert('我是需求点'); })
    .addTo(this.map);
  }


  // ngOnInit() {
  //   var map = L.map('map', {
  //     crs: L.CRS.EPSG3857,
  //     attributionControl: false,
  //     zoomControl: false
  //   }).setView([23.129529, 113.380216], 14);
  //   let basemap = L.esri.basemapLayer('GoogleVex');
  //   basemap.addTo(map);

  // }
  //路由设计
  routeButton() {
    if (!!this.flag) {
      this.flag = false;
    } else {
      this.flag = true;
    }
  }
  transferBoxMask() {
    if (this.flag == true) {
      this.flag = false;
    }
  }
  //跳转到路由规划
  routeDesign() {
    if (this.flag == true) {
      this.flag = false;
    }
    this.navCtrl.push(RouteDesignPage);
  }

  //跳转到交接箱详情
  transferbox() {
    if (this.flag == true) {
      this.flag = false;
    }
    this.navCtrl.push(TransferBoxPage);
  }

  //图层显示
  showLayer() {
    if (!!this.layerFlag) {
      this.layerFlag = false;
    } else {
      this.layerFlag = true;
    }
  }
  //图层遮罩层
  layerMask() {
    if (this.layerFlag == true) {
      this.layerFlag = false;
    }
  }
  addDemandPoint(){
    let mapCurZoom = this.map.getZoom();
    let mapCurCenter = this.map.getCenter();
    let modal = this.modalCtrl.create(PointAddModalPage, {
      zoom: mapCurZoom, 
      center: mapCurCenter
    });
    modal.onDidDismiss(data => {
      if(!!!data) return;
     let marker = L.marker(data.center);
     this.featureLayer.addLayer(marker);
     this.map.fitBounds(this.featureLayer.getBounds());
   });
    modal.present();
  }

  pointSearch(){
    this.navCtrl.push('PointSearchPage')
  }
  //选择资源
  selectTransferbox(){
    let modal = this.modalCtrl.create(TransferboxModalPage);
    modal.onDidDismiss(data => {
      if(!!!data) return;
     //let marker = L.marker(data.center);
     //this.featureLayer.addLayer(marker);
     //this.map.fitBounds(this.featureLayer.getBounds());
   });
    modal.present();
  }

  //新增交接箱
  addTransferbox(){
      let mapCurZoom = this.map.getZoom();
      let mapCurCenter = this.map.getCenter();
      let modal = this.modalCtrl.create(TransferboxAddModalPage, {
        zoom: mapCurZoom, 
        center: mapCurCenter
      });
      let mytransferbox = L.icon({
        iconUrl: 'assets/lib/images/transferbox-icon-2x.png',
        iconSize: [25, 41],
        iconAnchor: [12.5, 41],
        // popupAnchor: [-3, -76],
        //shadowUrl: 'assets/lib/images/marker-shadow.png',
        //shadowSize: [25, 41],
        //shadowAnchor: [20.5, 20.5]
    });
      modal.onDidDismiss(data => {
        if(!!!data) return;
      let marker = L.marker(data.center,{icon:mytransferbox, content: data.content});
      this.featureLayer.addLayer(marker);
      this.map.fitBounds(this.featureLayer.getBounds());
    });
      modal.present();
  }


}

// var showInfo = function (){
//   alert("按钮信息");
// }


