import { Component, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular';
//import { MySlide } from "../../components/my-side";
import { RouteDesignPage } from '../route-design/route-design';

declare var L: any;

@Component({
  selector: 'route-line',
  templateUrl: 'route-line.html',
  styleUrls: ['/pages/route-line/route-line.scss']
})
export class RouteLinePage implements OnInit {
  flag: boolean = true;
  resourceFlag: boolean = false;
  showlineFlag:boolean = false;
  pageNumber:number = 1;
  slides: Array<{title: string, length:string, distance:string}>;
  constructor(public navCtrl: NavController) {

  }
  ngOnInit() {

    var map = new L.map('routeLine', {
      crs: L.CRS.EPSG3857,
      attributionControl: false,
      zoomControl: false
    }).setView([23.129529, 113.380216], 14);
    let basemap = L.esri.basemapLayer('GaodeVec');
    basemap.addTo(map);

    this.slides = [];
    for(let i=0; i < 3; i++){
      this.slides.push({
        title:'XX交接箱 -> 1号需求点' + i,
        length:'123'+ i + '米',
        distance:'1'+ i + '公里'
      })
    }

  }

  //添加交接箱
  showline() {
    if (!!!this.showlineFlag) {
      this.showlineFlag = true;
    } else {
      this.showlineFlag = false;
    }
  }

  // closeline(){
  //   if (!!!this.showlineFlag) {
  //     this.showlineFlag = true;
  //   } else {
  //     this.showlineFlag = false;
  //   }
  // }

  designRoute(){
    this.navCtrl.push(RouteDesignPage);
  }

 




}



