import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { NavController,NavParams,AlertController, ViewController  } from 'ionic-angular';

declare var L: any;

@Component({
  selector: 'ledup-modal',
  templateUrl: 'ledup-modal.html'
})
export class LedupModalPage implements OnInit {
  pet: string = "ledup";
  testRadioResult:any;
  showPipelineFlag:boolean = false;
  private map: any;
  private selectData:any;
  @ViewChild('ledupModal')
  mapDiv: ElementRef;
  
  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController,public alertCtrl: AlertController) {
  }
  ngOnInit() {
    let VectorBasemap = L.esri.basemapLayer('GoogleVex');
    let SatelliteBasemap = L.esri.basemapLayer('GoogleSat');
    let VectorLayer = L.layerGroup([VectorBasemap]);
    let SatelliteLayer = L.layerGroup([SatelliteBasemap]);
    let BaseLayers = { "地图": VectorLayer, "影像": SatelliteLayer};
    let curZoom = this.navParams.get('zoom');
    let curCenter = this.navParams.get('center');

    this.map = new L.map(this.mapDiv.nativeElement, {
      center: curCenter,
      zoom: curZoom,
      crs: L.CRS.EPSG3857,
      attributionControl: false,
      zoomControl: false,
      layers: [VectorLayer]
    });
    L.control.layers(BaseLayers, null).addTo(this.map);
  }
  dismiss() {
    this.viewCtrl.dismiss();
  }
  submit(){
    let mapCurZoom = this.map.getZoom();
    let mapCurCenter = this.map.getCenter();
    this.viewCtrl.dismiss({
      center: mapCurCenter,
      zoom: mapCurZoom
    });
  }

  showResourceRadio(){
    let alert = this.alertCtrl.create();
    alert.setTitle('选择资源');

    alert.addInput({
      type: 'radio',
      label: '光缆管道',
      value: 'guangdao',
      checked: true
    });
    alert.addInput({
      type: 'radio',
      label: '杠路吊线',
      value: 'ganglu'
    });
    alert.addInput({
      type: 'radio',
      label: '墙壁吊线',
      value: 'qiangbi'
    });
    alert.addButton('取消');
    alert.addButton({
      text: '确定',
      handler: data => {
        //this.testRadioOpen = false;
       //this.testRadioResult = data;
       if(data == 'guangdao'){
         this.showPipelineFlag = true;
       }
      }
    });
    alert.present();
  }
  closePipeline(){
    if (!!!this.showPipelineFlag) {
      this.showPipelineFlag = true;
    } else {
      this.showPipelineFlag = false;
    }
  }

}



