import { Component } from '@angular/core';
import { NavController, Platform } from 'ionic-angular';

import { ProjectHistoryDetailPage } from '../project-historyDetail/project-historyDetail'; 

@Component({
  selector: 'project-history',
  templateUrl: 'project-history.html'
})
export class ProjectHistoryPage {
  placeholder:string = "请输入项目名称、客户单位、地点";
  isAndroid: boolean = false;
  constructor(public navCtrl: NavController,platform: Platform) {
    this.isAndroid = platform.is('android');
  }

  projectHistoryDetail(){
    this.navCtrl.push(ProjectHistoryDetailPage)
  }

}



