import { Component, OnInit, ViewChild } from '@angular/core';
import { NavController,NavParams, ViewController  } from 'ionic-angular';

import { RouteLinePage } from '../../route-line/route-line';
import { TransferBoxPage } from '../../transferbox/transferbox';

@Component({
  selector: 'transferbox-modal',
  templateUrl: 'transferbox-modal.html'
})
export class TransferboxModalPage implements OnInit {
  pet: string = "transferbox";
  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController) {
  }
  ngOnInit() {
    
  }
  dismiss() {
    this.viewCtrl.dismiss();
  }

  跳转到路由规划
  routeDesign() {
    this.navCtrl.push(RouteLinePage);
  }

  //跳转到交接箱详情
  transferbox(){
    this.navCtrl.push(TransferBoxPage);
  }

}



