import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { CoreBitPage } from '../core-bit/core-bit';
import { PicResourcesPage } from '../pic-resources/pic-resources';

@Component({
  selector: 'transferbox',
  templateUrl: 'transferbox.html',
  styleUrls:['/pages/transferbox/transferbox.scss']
})
export class TransferBoxPage {

  constructor(public navtrl:NavController){

  }
  //选择芯位 
  corebit(){
    this.navtrl.push(CoreBitPage);
  }
  //查看资源点图片
  resources(){
    this.navtrl.push(PicResourcesPage);
  }
  //查看附件项目
  // projectNear(){
  //   this.navtrl.push(ProjectNearPage);
  // }
}





